import React from 'react'

function Card({ele}) {
  return (
    <>
           <div className="col-md-3 offset-1">
                    <div className="card text-black">
                        <i className="fab fa-apple fa-lg pt-3 pb-1 px-3"></i>
                        <img src={`file:/D:/Mayuresh/js/${ele.image.slice(13)}`}
                            className="card-img-top" alt="Apple Computer" />
                        <div className="card-body">
                            <div className="text-center">
                                <h5 className="card-title">{ele.name}</h5>
                                <p className="text-muted mb-2">${ele.price} </p>
                            </div>
                            <div>
                                <div className="d-flex justify-content-center">
                                    <button className='btn-bg-primary'>-</button>
                                    <span>{ele.quantity}</span>
                                    <button className='btn-bg-primary'>+</button>
                                </div>
                            </div>
                            <div className="d-flex justify-content-center mt-2">
                                <button className="btn bg-primary">Add to bag</button>
                            </div>
                        </div>
                    </div>
                </div>
    </>
  )
}

export default Card