import React, { useState } from 'react'
import Card from '../components/Card'
import CartShop from '../components/CartShop'
import BuyForm from '../components/BuyForm'

function Task1() {
    const [show, setShow] = useState(false)
    const handleForm = () => {
        setShow(!show)
    }
    const dataArray=JSON.parse(localStorage.getItem("task1"))

    return (
        <>
            <div className='container'>
                <button className="btn bg-primary text-white float-right" onClick={handleForm}>Buy</button>
                {show && <BuyForm />}
                {!show &&  <div className="row pt-2">
                    {dataArray?.map((ele)=>{
                        return <Card ele={ele}/>
                    })}
                    
                </div>}
               
            </div>
            {!show &&    <CartShop />}
          

        </>


    )
}

export default Task1