import React, { useEffect, useState } from 'react'
import { v4 as uuidv4 } from 'uuid';
 

function BuyForm() {
    const [data, setData] = useState({
        name: "",
        price: "",
         
    })
    const [dataArray,setDataArray]=useState([])
   
    const handleChange = (e) => {
        const { name, value } = e.target;
        setData((pre) => ({ ...pre, [name]: value }))
    }
    const handleImage=(e)=>{
        setData((pre)=>({...pre,image:e.target.value}))
    }
    const handleSubmit = (e) => {
        e.preventDefault();
        setDataArray((pre)=>[...pre,{...data,id:uuidv4(),quantity:1}])
    }
    // console.log(dataArray)
    useEffect(()=>{
        localStorage.setItem("task1",JSON.stringify(dataArray))
    },[dataArray])
    
    return (
        <div className='container row '>
            <div className="col-md-6 offset-3 border shadow mt-5 py-5 px-3">
                <form onSubmit={handleSubmit}>
                    <div className="row">
                        <div className="col-md-6">
                            <div className="form-group">
                                <label htmlFor="name">Name</label>
                                <input onChange={handleChange} value={data.name} type="text" name="name" id="name" className='form-control' />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group">
                                <label htmlFor="price">Price</label>
                                <input onChange={handleChange} value={data.price} type="number" name="price" id="price" className='form-control' />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group">
                                <label htmlFor="image">Upload Image</label>
                                <input onClick={handleImage}   type="file" name="image" id="image" className='form-control' />
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-6 mt-3">
                            <button className="btn bg-primary text-white">Add Product</button>
                        </div>
                    </div>

                </form>
            </div>

        </div>
    )
}

export default BuyForm